import { useEffect, useRef, useState } from "react";
import * as faceapi from "face-api.js";

export const useFaceDetection = ({
  videoRef,
  isActive,
  onFaceCountChange,
}: {
  videoRef: React.RefObject<HTMLVideoElement | null>;
  isActive: boolean;
  onFaceCountChange?: (count: number) => void;
}) => {
  const [modelsLoaded, setModelsLoaded] = useState(false);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const smoothBufferRef = useRef<number[]>([]);

  useEffect(() => {
    faceapi.nets.tinyFaceDetector
      .loadFromUri("/models")
      .then(() => setModelsLoaded(true));
  }, []);

  useEffect(() => {
    if (!isActive || !modelsLoaded) return;

    const detect = async () => {
      const video = videoRef.current;
      if (!video || video.readyState !== 4) return;

      const detections = await faceapi.detectAllFaces(
        video,
        new faceapi.TinyFaceDetectorOptions({
          inputSize: 320,
          scoreThreshold: 0.6,
        })
      );

      smoothBufferRef.current.push(detections.length);
      if (smoothBufferRef.current.length > 5)
        smoothBufferRef.current.shift();

      const avg =
        smoothBufferRef.current.reduce((a, b) => a + b, 0) /
        smoothBufferRef.current.length;

      onFaceCountChange?.(Math.round(avg));
    };

    intervalRef.current = setInterval(detect, 150);

    return () => {
      clearInterval(intervalRef.current!);
      intervalRef.current = null;
    };
  }, [isActive, modelsLoaded, videoRef, onFaceCountChange]);

  return { canvasRef };
};
