"use client";

import Image from "next/image";
import { useEffect, useRef, useState } from "react";
import { useRouter } from "next/navigation";

import { cn } from "@/lib/utils";
import { vapi } from "@/lib/vapi.sdk";
import { interviewer } from "@/constants";
import { createFeedback } from "@/lib/actions/general.action";
import CameraView from "@/components/CameraView";
import CameraButton from "@/components/CameraButton";
import { toast } from "sonner";

enum CallStatus {
  INACTIVE = "INACTIVE",
  CONNECTING = "CONNECTING",
  ACTIVE = "ACTIVE",
  FINISHED = "FINISHED",
}

interface SavedMessage {
  role: "user" | "system" | "assistant";
  content: string;
}

const Agent = ({
  userName,
  userId,
  interviewId,
  feedbackId,
  type,
  questions,
}: AgentProps) => {
  const router = useRouter();

  const [callStatus, setCallStatus] = useState(CallStatus.INACTIVE);
  const [messages, setMessages] = useState<SavedMessage[]>([]);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [lastMessage, setLastMessage] = useState("");
  const [isCameraActive, setIsCameraActive] = useState(false);
  const [faceCount, setFaceCount] = useState(0);
  const [showViolationWarning, setShowViolationWarning] = useState(false);

  /* ---------------- Anti-spam refs ---------------- */
  const noFaceTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const noFaceWarnedRef = useRef(false);

  /* ---------------- VAPI listeners ---------------- */
  useEffect(() => {
    vapi.on("call-start", () => setCallStatus(CallStatus.ACTIVE));
    vapi.on("call-end", () => setCallStatus(CallStatus.FINISHED));

    vapi.on("message", (message: Message) => {
      if (message.type === "transcript" && message.transcriptType === "final") {
        setMessages((prev) => [
          ...prev,
          { role: message.role, content: message.transcript },
        ]);
      }
    });

    vapi.on("speech-start", () => setIsSpeaking(true));
    vapi.on("speech-end", () => setIsSpeaking(false));
    vapi.on("error", console.error);

    return () => vapi.removeAllListeners();
  }, []);

  /* ---------------- Handle end of call ---------------- */
  useEffect(() => {
    if (messages.length) {
      setLastMessage(messages[messages.length - 1].content);
    }

    if (callStatus !== CallStatus.FINISHED) return;

    const generateFeedback = async () => {
      if (type === "generate") return router.push("/");

      const { success } = await createFeedback({
        interviewId: interviewId!,
        userId: userId!,
        transcript: messages,
        feedbackId,
      });

      router.push(success ? `/interview/${interviewId}/feedback` : "/");
    };

    generateFeedback();
  }, [callStatus, messages, type, router, interviewId, userId, feedbackId]);

  /* ---------------- Face count logic ---------------- */
  const handleFaceCountChange = (count: number) => {
    setFaceCount(count);

    if (callStatus !== CallStatus.ACTIVE) return;

    /* ---- MULTIPLE PEOPLE ---- */
    if (count > 1) {
      setShowViolationWarning(true);
      toast.error(`⚠️ Multiple people detected (${count})`);
      clearTimeout(noFaceTimeoutRef.current!);
      noFaceWarnedRef.current = false;
      return;
    }

    setShowViolationWarning(false);

    /* ---- NO FACE (10s delay, once) ---- */
    if (count === 0) {
      if (!noFaceTimeoutRef.current && !noFaceWarnedRef.current) {
        noFaceTimeoutRef.current = setTimeout(() => {
          toast.warning("⚠️ No face detected in frame");
          noFaceWarnedRef.current = true;
        }, 10_000);
      }
    } else {
      clearTimeout(noFaceTimeoutRef.current!);
      noFaceTimeoutRef.current = null;
      noFaceWarnedRef.current = false;
    }
  };

  /* ---------------- Call controls ---------------- */
  const handleCall = async () => {
    if (!isCameraActive) {
      toast.error("Start your camera first");
      return;
    }

    setCallStatus(CallStatus.CONNECTING);

    if (type === "generate") {
      await vapi.start(undefined, undefined, undefined,
        process.env.NEXT_PUBLIC_VAPI_WORKFLOW_ID!, {
          variableValues: { username: userName, userid: userId },
        }
      );
    } else {
      const formattedQuestions = questions?.map(q => `- ${q}`).join("\n") || "";
      await vapi.start(interviewer, {
        variableValues: { questions: formattedQuestions },
      });
    }
  };

  const handleDisconnect = () => {
    vapi.stop();
    setCallStatus(CallStatus.FINISHED);
  };

  /* ---------------- UI ---------------- */
  return (
    <>
      {showViolationWarning && (
        <div className="mb-4 bg-red-600 text-white px-6 py-3 rounded-lg text-center font-bold animate-pulse">
          ⚠️ MULTIPLE PEOPLE DETECTED
        </div>
      )}

      <div className="call-view">
        <div className="card-interviewer">
          <div className="avatar">
            <Image src="/logo.png" alt="AI" width={60} height={50} />
            {isSpeaking && <span className="animate-speak" />}
          </div>
          <h3>AI Interviewer</h3>
        </div>

        <CameraView
          isActive={isCameraActive}
          enableFaceDetection={callStatus === CallStatus.ACTIVE}
          onFaceCountChange={handleFaceCountChange}
        />
      </div>

      {lastMessage && (
        <div className="transcript-border">
          <p className="animate-fadeIn">{lastMessage}</p>
        </div>
      )}

      <div className="flex justify-center gap-4 mt-6">
        <CameraButton
          isActive={isCameraActive}
          onClick={() => setIsCameraActive(v => !v)}
          disabled={callStatus === CallStatus.ACTIVE}
        />

        {callStatus === CallStatus.ACTIVE ? (
          <button className="btn-disconnect" onClick={handleDisconnect}>
            End
          </button>
        ) : (
          <button className="btn-call" onClick={handleCall}>
            {callStatus === CallStatus.CONNECTING ? "..." : "Call"}
          </button>
        )}
      </div>
    </>
  );
};

export default Agent;
